import React from "react"

import twitterIcon from "../../../assets/social-icons/twitter.svg"
import facebookIcon from "../../../assets/social-icons/facebook.svg"
import instagramIcon from "../../../assets/social-icons/instagram.svg"
import linkedinIcon from "../../../assets/social-icons/linkedin.svg"
import whatsappIcon from "../../../assets/social-icons/whatsapp.svg"

import { Container } from "./style"

import { translate } from "../../utils/translate"

const ThankYou: React.FC = ({ children }) => {
	return (
		<Container className="container">
			<div className="content-page">{children}</div>

			<button>{translate("thankyou.contact")}</button>

			<div className="social-wrapper">
				<a
					href="https://www.facebook.com/kivalitaconsulting/"
					target="_blank"
					rel="noopener noreferrer"
				>
					<img src={facebookIcon} alt="Facebook" />
				</a>
				<a
					href="https://www.instagram.com/kivalitaconsulting/"
					target="_blank"
					rel="noopener noreferrer"
				>
					<img src={instagramIcon} alt="Instagram" />
				</a>
				<a
					href="https://www.linkedin.com/company/kivalita-consulting/"
					target="_blank"
					rel="noopener noreferrer"
				>
					<img src={linkedinIcon} alt="LinkedIn" />
				</a>
				<a
					href="https://api.whatsapp.com/send?phone=5511943068327"
					target="_blank"
					rel="noopener noreferrer"
				>
					<img src={whatsappIcon} alt="Whatsapp" />
				</a>
				<a
					href="https://twitter.com/kivalita"
					target="_blank"
					rel="noopener noreferrer"
				>
					<img src={twitterIcon} alt="Twitter" />
				</a>
			</div>

			<div className="content-link">
				<a href="/">{translate("thankyou.go-back-site")}</a>
			</div>
		</Container>
	)
}

export default ThankYou
