import React from "react"

import Layout from "../components/Layout"
import ThankYou from "../components/pages/ThankYou"
import SEO from "../components/utils/SEO"

import { translate } from "../components/utils/translate"
import { IBreadCrumbOption } from "../interfaces"

const ThankYouAboutUsPage = () => {
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "thank-you",
			name: translate("breadCrumb.thank-you"),
			isActive: false,
		},
	]

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO></SEO>

			<ThankYou>
				<h2>{translate("thankyou.thank-you-here")}</h2>
				<p>{translate("thankyou.important-legislations")}</p>
				<p>
					{translate("thankyou.know-more")} <br />
					<a href="https://www.kivalitaconsulting.com/legislation/">
						https://www.kivalitaconsulting.com/legislation/
					</a>
				</p>
				<p>{translate("thankyou.quality-company")}</p>
			</ThankYou>
		</Layout>
	)
}

export default ThankYouAboutUsPage
