import styled from "styled-components"

export const Container = styled.div`
	text-align: center;

	.content-page {
		margin-top: 32px;
		h2 {
			color: #444;
			font-family: "Montserrat", Sans-serif;
			font-size: 35px;
			font-weight: 600;
			line-height: 1.4em;
		}

		p {
			color: #444;
			font-family: "Montserrat", Sans-serif;
			font-size: 20px;
			font-weight: 500;
			line-height: 1.4em;
			max-width: 600px;
			margin: 40px auto;
			line-height: 30px;

			a {
				color: rgb(17, 89, 100);
			}
		}
	}

	button {
		font-family: "Poppins", Sans-serif;
		font-size: 17px;
		font-weight: 600;
		fill: #fff;
		color: #fff;
		text-transform: uppercase;
		background-color: #18bdd7;
		border-radius: 5px 5px 5px 5px;
		padding: 20px 80px 20px 80px;
		border: 0;
		outline: 0;
		margin: 16px 0;
	}

	.content-link {
		margin-bottom: 32px;

		a {
			color: rgb(17, 89, 100);
		}
	}

	.social-wrapper {
		margin: 48px 0;
		a {
			padding: 24px;
			margin: 0 8px;

			img {
				width: 64px;
				height: 64px;
			}
		}
	}
`
